"use client";
import { useState, useEffect } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Progress } from '@/components/ui/progress';
import { Card } from '@/components/ui/card';
import { Alert, AlertTitle, AlertDescription } from '@/components/ui/alert';
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer, CartesianGrid } from 'recharts';
import { Activity, AlertTriangle, CheckCircle2, Loader2 } from 'lucide-react';
import { toast } from 'sonner'; // ✅ 引入提示组件
// ✅ 引入后端 Actions
import { saveAssessment, getAssessmentHistory } from '@/app/actions';

const QUESTIONS = [
  "做事时提不起劲或没有兴趣",
  "感到心情低落、沮丧或绝望",
  "入睡困难、睡不安稳或睡眠过多",
  "感到疲倦或没有活力",
  "食欲不振或吃太多",
];

export default function AssessmentPage() {
  const [step, setStep] = useState(0);
  const [answers, setAnswers] = useState<number[]>([]);
  const [result, setResult] = useState<number | null>(null);
  
  // ✅ 改用 API 数据
  const { currentUser } = useAppStore();
  const [history, setHistory] = useState<any[]>([]);
  const [loadingHistory, setLoadingHistory] = useState(true);

  // ✅ 加载历史记录 (带安全检查)
  useEffect(() => {
    async function fetchHistory() {
      if (currentUser?.id) {
        try {
          const data = await getAssessmentHistory(currentUser.id);
          // 🛡️ 双重保险：确保是数组，否则给空数组，防止图表崩溃
          if (Array.isArray(data)) {
            setHistory(data);
          } else {
            setHistory([]);
          }
        } catch (error) {
          console.error("加载历史失败:", error);
          setHistory([]);
        } finally {
          setLoadingHistory(false);
        }
      }
    }
    fetchHistory();
  }, [currentUser, result]); // 当 result 更新时（刚测完），也刷新历史

  const handleAnswer = async (score: number) => {
    const newAnswers = [...answers, score];
    
    // 如果还没到最后一题
    if (step < QUESTIONS.length - 1) {
      setAnswers(newAnswers);
      setStep(step + 1);
    } else {
      // 最后一题，计算结果并保存
      const total = newAnswers.reduce((a, b) => a + b, 0);
      setResult(total);
      
      // ✅ 存入数据库
      if (currentUser?.id) {
        try {
          await saveAssessment(
            currentUser.id,
            total,
            total > 10 ? '需关注' : '正常'
          );
          toast.success("测评完成", { description: "结果已保存至云端档案" });
        } catch (error) {
          toast.error("保存失败", { description: "网络连接可能不稳定" });
        }
      }
    }
  };

  const isHighRisk = result !== null && result > 12;

  return (
    <div className="space-y-8 pb-10">
      <div className="flex flex-col md:flex-row justify-between items-end border-b border-gray-100 pb-4">
        <div>
          <h1 className="text-2xl font-bold text-[#2C3E50] flex items-center gap-2">
            <Activity className="w-6 h-6 text-[#5D9C59]" /> 心理测评中心
          </h1>
          <p className="text-slate-500 mt-1 text-sm">科学评估您的心理状态，数据已加密存储于云端</p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* 左侧：测评区 */}
        <div className="lg:col-span-2">
           <div className="bg-white rounded-2xl shadow-sm border border-slate-100 p-8 min-h-[400px] flex flex-col justify-center">
             {result === null ? (
               <>
                 <div className="mb-8">
                   <div className="flex justify-between text-sm font-medium text-slate-500 mb-2">
                     <span>进度</span>
                     <span>{step + 1} / {QUESTIONS.length}</span>
                   </div>
                   <Progress value={(step / QUESTIONS.length) * 100} className="h-2 bg-slate-100" />
                 </div>
                 <h2 className="text-2xl font-bold text-[#2C3E50] mb-8 leading-relaxed text-center">
                   “ 在过去的两周里，您有多少时间<br/>{QUESTIONS[step]}？ ”
                 </h2>
                 <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                   {[0, 1, 2, 3].map((score) => (
                     <button 
                       key={score} 
                       className="p-4 rounded-xl border-2 border-slate-100 hover:border-[#5D9C59] hover:bg-[#F0F7EF] hover:text-[#3E6D3B] transition-all text-left font-medium text-slate-600"
                       onClick={() => handleAnswer(score)}
                     >
                       {['完全不会', '只有几天', '一半以上时间', '几乎每天'][score]}
                     </button>
                   ))}
                 </div>
               </>
             ) : (
               <div className="text-center animate-in fade-in zoom-in duration-500">
                 <div className="w-20 h-20 mx-auto bg-[#F0F7EF] rounded-full flex items-center justify-center mb-6">
                   <CheckCircle2 className="w-10 h-10 text-[#5D9C59]" />
                 </div>
                 <h2 className="text-3xl font-bold text-[#2C3E50] mb-2">测评完成</h2>
                 <p className="text-slate-500 mb-8">PHQ-9 抑郁症筛查量表</p>
                 <div className="bg-slate-50 p-6 rounded-xl max-w-md mx-auto mb-8">
                   <div className="text-sm text-slate-500 mb-1">您的得分</div>
                   <div className={`text-5xl font-bold ${isHighRisk ? 'text-red-500' : 'text-[#5D9C59]'}`}>{result}</div>
                   <div className="mt-2 font-medium text-slate-700">
                     {isHighRisk ? "情绪波动较大" : "心理状态良好"}
                   </div>
                 </div>
                 {isHighRisk && (
                   <Alert variant="destructive" className="bg-red-50 border-red-100 text-left mb-6 max-w-md mx-auto">
                     <AlertTriangle className="h-4 w-4" />
                     <AlertTitle>温馨提示</AlertTitle>
                     <AlertDescription>系统检测到您最近压力可能较大。建议您预约咨询师聊聊。</AlertDescription>
                   </Alert>
                 )}
                 <Button onClick={() => {setResult(null); setStep(0); setAnswers([])}} variant="outline">返回重测</Button>
               </div>
             )}
           </div>
        </div>

        {/* 右侧：历史趋势图 */}
        <div className="space-y-6">
          <Card className="border-0 shadow-sm">
            <div className="p-6 border-b border-slate-50"><h3 className="font-bold text-[#2C3E50]">历史趋势</h3></div>
            <div className="p-6">
              {loadingHistory ? (
                <div className="h-[200px] flex items-center justify-center text-slate-400">
                  <Loader2 className="animate-spin w-6 h-6 mr-2" /> 加载数据中...
                </div>
              ) : history.length > 0 ? (
                <div className="h-[200px] w-full">
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart data={history}>
                      <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#E2E8F0" />
                      <XAxis 
                        dataKey="createdAt" 
                        tickFormatter={(v) => new Date(v).toLocaleDateString(undefined, {month:'numeric', day:'numeric'})} 
                        tick={{fontSize: 10}} 
                        axisLine={false} 
                        tickLine={false} 
                      />
                      <YAxis tick={{fontSize: 10}} axisLine={false} tickLine={false} />
                      <Tooltip labelFormatter={(v) => new Date(v).toLocaleDateString()} />
                      <Line type="monotone" dataKey="score" stroke="#5D9C59" strokeWidth={3} dot={{r: 4, fill: '#5D9C59'}} />
                    </LineChart>
                  </ResponsiveContainer>
                </div>
              ) : (
                <div className="h-[200px] flex flex-col items-center justify-center text-slate-400 text-sm">
                  <Activity className="w-8 h-8 mb-2 opacity-50" />暂无历史测评数据
                </div>
              )}
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
}